import React, { useState } from 'react';
import { Button, Modal, ModalHeader, ModalBody, ModalFooter,Form,FormGroup,Row,Col,Label,CustomInput,Input,Card,Table } from 'reactstrap';
import "./Modal.css"
const Extra = (props) => {
  const {
    className
  } = props;

  const [modal, setModal] = useState(false);

  const toggle = () => setModal(!modal);

  const closeBtn = <button className="close" onClick={toggle}>&times;</button>;

  return (
  <div>
    <Button color="success" onClick={toggle} >Extra Lectures</Button>
    <Modal isOpen={modal} toggle={toggle} contentClassName="attendence-modal">
      <ModalHeader toggle={toggle} close={closeBtn}>Attendence
          
                 </ModalHeader>
      <ModalBody>
      <div className="container">
          <Col md={6}>
                  <FormGroup>
                      <Label for="class">Choose Date</Label>
                      <Input type="date" id="date" />
                  </FormGroup>
                  </Col>
                  
          
            <Table hover>
      <thead>
        <tr>
          <th>S.No</th>
          <th>Student ID</th>
          <th>Present</th>
          <th>Absent</th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <th scope="row">1</th>
          <td>17Rh1a05b0</td>
          <td>
                     <FormGroup check>
                          <Input type="checkbox" name="check" id="exampleCheck"/>
        
                          </FormGroup>
          </td>
          <td>
               <FormGroup check>
                          <Input type="checkbox" name="check" id="exampleCheck"/>
        
                          </FormGroup>
          

          </td>
        </tr>
        <tr>
          <th scope="row">2</th>
          <td>Jacob</td>
          <td>
                       <FormGroup check>
                          <Input type="checkbox" name="check" id="exampleCheck"/>
        
                          </FormGroup>
        </td>
          <td>
                  <FormGroup check>
                          <Input type="checkbox" name="check" id="exampleCheck"/>
        
                          </FormGroup>
          </td>
        </tr>
        <tr>
          <th scope="row">3</th>
          <td>Larry</td>
          <td>
               <FormGroup check>
                          <Input type="checkbox" name="check" id="exampleCheck"/>
        
                          </FormGroup>
          </td>
          <td>
                  <FormGroup check>
                          <Input type="checkbox" name="check" id="exampleCheck"/>
        
                          </FormGroup>
           </td>
        </tr>
       
      </tbody>
    </Table>
          
      </div>
      </ModalBody>
      <ModalFooter>
        <Button type= "submit" color="primary" onClick={toggle}>Add</Button>{' '}
        <Button color="secondary" onClick={toggle}>Cancel</Button>
      </ModalFooter>
    </Modal>
  </div>
);
}

export default Extra;
